.main-container {
    &__header {
        font-family: Zlusa;
        font-size: 40px;
        font-weight: 400;
        text-align: center;
        color: white;
        text-decoration: underline; }
    &__description {
        font-family: Zlusa;
        font-size: 25px;
        color: white;
        padding: 2px 10px;
        text-align: center;
        opacity: 0.9;
        &--hidden {
            height: 0;
            overflow: hidden; } } }
